import { Component, OnInit, Inject, ViewEncapsulation, Renderer2 } from '@angular/core';
import { ActivatedRoute, Data, NavigationEnd, Router } from '@angular/router';

import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-public',
  templateUrl: './public.component.html',
  styleUrls: ['./public.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class PublicComponent implements OnInit {

  data:any;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private router: Router, 
    private renderer: Renderer2, private sharedService:SharedService ) { 

      router.events.subscribe((event: any) => {
        if (event instanceof NavigationEnd) {
          window.scrollTo(0, 0);
        }
      });

    }

  ngOnInit(): void {

    // Subscribe to the resolved route data
    this._activatedRoute.data.subscribe((data: Data) => {
      
      this.data = data.initialData;

      document.querySelector('body').className = '';
      this.renderer.addClass(document.body, this.data.module)
      this.renderer.addClass(document.body, this.data.page)
    
      window.scrollTo(0, 0);

    });

  }

}
