import { Injectable, Inject} from '@angular/core';
import { Router, NavigationEnd, ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { forkJoin, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { DOCUMENT } from '@angular/common';
import { SharedService } from 'src/app/shared/services/shared.service';

@Injectable({
  providedIn: 'root'
})
export class PublicResolversService implements Resolve<any> {

  path:any;

  constructor(
    private router: Router, 
    @Inject(DOCUMENT) element, 
    private sharedService:SharedService
  ) { }


  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any{ 

    var path = state.url.replace('/','');

    var data = {
      module: 'condo-theme-public',
      page: path
    }
    return data
    
  }

}
