import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs/internal/Observable';
import { startWith } from 'rxjs/internal/operators/startWith';
import { map } from 'rxjs/internal/operators/map';

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';

import { VERSION } from '@angular/material/core';

import * as moment from 'moment';
import { CountryISO, SearchCountryField } from 'ngx-intl-tel-input';

@Component({
  selector: 'app-join',
  templateUrl: './join.component.html',
  styleUrls: ['./join.component.scss']
})
export class joinComponent implements OnInit {

  version = VERSION;
  myControl: FormControl = new FormControl();
  lstApartments:any;
  filteredOptions: Observable<string[]>;
  isApartmentSelected :boolean = false;
  user:any = {};
  lstUserType:any;
  userType:string = null;
  apartmentDetails:any;
  searchApartmentText:string = "";
  unitName:string = "";
  BlockName:string = "";
  isUserSubmitted:boolean = false;
  successImage:boolean = false;

  isCaptcha:boolean = false;
  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  phoneForm = new FormGroup({
    phone: new FormControl(undefined, [Validators.required])
  });
  preferredCountries: CountryISO[] = [CountryISO.UnitedStates, CountryISO.UnitedKingdom];

  constructor(
    private router: Router,
    private apartmentservice:ApartmentService, 
    private userService:UserService,
    private sharedService:SharedService) 
    { 
      this.loadStates();
      this.lstUserType = [{usrValue:2,usrName:"Tenant"},{usrValue:4,usrName:"Owner"}];

      router.events.subscribe((event: any) => {
        if (event instanceof NavigationEnd) {
          if(window.location.href.indexOf('senthil') || window.location.href.indexOf('localhost') != -1){
            this.isCaptcha = false
          }
          else {
            this.isCaptcha = true
          }
        }
      });

    }

  resolved(captchaResponse: string) {
  }
    
  loadStates() {
    this.apartmentservice.getAllApartmentsPublic().subscribe((res : any) => {
      this.lstApartments = res;
    },
    error => {
      console.log(error);
    }
    );
  }

  filter(val: string): string[] {
    if(this.lstApartments && this.lstApartments.length &&val.length>2){
      return this.lstApartments.filter(option =>
        option.apartmentName.toLowerCase().indexOf(val.toLowerCase()) === 0);
    }
  }

  onSelectionChanged(evt){
    this.isApartmentSelected = false;
    this.unitName = this.BlockName = this.userType = "";
    this.user = {};
    this.apartmentDetails = {};
    this.apartmentDetails = this.lstApartments.filter(itm=>itm.apartmentName.toLowerCase() === evt.option.value.toLowerCase())[0];
    this.isApartmentSelected = true;
    this.searchApartmentText = "";
  }

  cancelSignup(){
    this.isApartmentSelected = false;
  }

  submitAddResidentForm(form) {
    var notesStrVal  =`{"UserType":"${this.userType}","BlockName":"${this.BlockName}","UnitName":"${this.unitName}"}`;
    this.isUserSubmitted = true;
    
      //add user
      let userDetails = {
        "apartmentId": parseInt(this.apartmentDetails.apartmentId),
        "firstName":this.user.firstName,
        "lastName": this.user.lastName,
        "emailId": this.user.emailId,
        "genderName": this.user.genderId.toString(),
        "phoneNumber": this.phoneForm.value.phone.number,
        "notes": notesStrVal,
        "comments": "",
        "isActive": true,
        "insertedOn": moment().format(),
        "updatedOn": "",
        "phonecountrycode": this.phoneForm.value.phone.countryCode,
        "signupSubNotes": [
          {
            "signupSubNotesId": 0,
            "signupUserRequestId": 0,
            "userType": this.userType,
            "blockUnit": this.BlockName,
            "unit": this.unitName,
            "genderName": this.user.genderId.toString()
          }
        ]
      }
    
      let params = {
        signupUser: userDetails
      }

      this.userService.addSignupUserRequest(params).subscribe((res:any) => {
            if(res.message) {
              setTimeout(() => {
                this.sharedService.setAlertMessage("Signup has been created successfull.");
              }, 200);
              this.isApartmentSelected = false;
              this.unitName = this.BlockName = this.userType = "";
              this.user = {};
              this.successImage = true
              this.searchApartmentText = "";

            }
            else {
              this.isUserSubmitted = false;
            }

      },
      error => {
        console.log(error);
      }
      );
  
  }

  backToJoin(){
    this.isApartmentSelected = true;
    this.successImage = false;
    this.isUserSubmitted = false;
  }

  ngOnInit() {
  
    this.filteredOptions = this.myControl.valueChanges
    .pipe(
      startWith(''),
      map(val => this.filter(val))
    );

  }

}
