import { Component, OnInit, Input, ViewEncapsulation, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-porfolio-cards',
  templateUrl: './porfolio-cards.component.html',
  styleUrls: ['./porfolio-cards.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PorfolioCardsComponent implements OnInit {

  @Input() items:any;
  @Input() index:any;

  itemCards:any;

  type:any;

  constructor() { }



  ngOnInit(): void {

  	

  }

  ngOnChanges(changes: SimpleChanges) {

  	this.itemCards = this.items[this.index].cards;
  	this.type = this.items[this.index].type;
  	
  }

}
